import React from 'react';
// assets
import personalPortfolioDemo from '../../../../assets/Portfolio/fcc/personalportfolio_demo.jpg';
import productLandingPageDemo from '../../../../assets/Portfolio/fcc/productlandingpage_demo.jpg';
import technicalDocumentationPageDemo from '../../../../assets/Portfolio/fcc/technicaldocumentationpage_demo.png';
import surveyFormDemo from '../../../../assets/Portfolio/fcc/surveyform_demo.jpg';
// components
import ProjectsGrid from './ProjectsGrid';
import PortfolioItem from './PortfolioItem';

const FreecodecampProjects = () => {
  return (
    <ProjectsGrid>
      <PortfolioItem 
          title='Personal portfolio'
          description={null}
          stack='html/css, vanilla js'
          image={personalPortfolioDemo}
          githubLink='https://github.com/armanDark/fcc-personal-portfolio'
          demoLink='https://armandark.github.io/fcc-personal-portfolio/'
        />
        <PortfolioItem 
          title='Product landing page'
          description={null}
          stack='html/css, vanilla js'
          image={productLandingPageDemo}
          githubLink='https://github.com/armanDark/fcc-product-landing-page'
          demoLink='https://armandark.github.io/fcc-product-landing-page/'
        />
        <PortfolioItem 
          title='Technical documentation page'
          description={null}
          stack='html/css, vanilla js'
          image={technicalDocumentationPageDemo}
          githubLink='https://github.com/armanDark/fcc-technical-documentation-page'
          demoLink='https://armandark.github.io/fcc-technical-documentation-page/'
        />
        <PortfolioItem 
          title='Survey form'
          description={null}
          stack='html/css'
          image={surveyFormDemo}
          githubLink='https://github.com/armanDark/fcc-survey-form'
          demoLink='https://armandark.github.io/fcc-survey-form/'
        />
    </ProjectsGrid>
  );
};

export default FreecodecampProjects;
