import React, { useContext } from 'react';
import styled from 'styled-components';
import SocialMediaButtons from 'react-social-media-buttons';
// context
import { StateContext } from '../../Root';

const Wrapper = styled.div`
  .contactme-info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    > * {
      margin-top: 2rem;
    }
    .email {
      width: fit-content;
      display: flex;
      justify-content: space-around;
      label {
        margin-right: 2rem;
      }
      a {
        color: ${props => props.color};
      }
    }
  }
`;

const ContactMe = () => {
  const { state, dispatch } = useContext(StateContext);

  return (
    <Wrapper id='contactme' color={state.currentColor}>
      <h2>Contact Me</h2>    
      <div className='contactme-info'>
        <SocialMediaButtons 
          links={[
            'https://github.com/armanDark',
            'https://www.facebook.com/arman.grigoryan.7311',
            'https://twitter.com/ArmanGr77307196',
            'https://www.instagram.com/arman.dark/'
          ]}
          buttonStyle={{width: '48px', height: '48px', margin: '0px 10px', backgroundColor: 'transparent'}}
          iconStyle={{color: `#fff`}}
          openNewTab={true}
        />
        <div className='email' > 
          <label>Send an email: </label>
          <a href='mailto:armangrigoryan7311@gmail.com'>armangrigoryan7311@gmail.com</a>
        </div>

      </div>
    </Wrapper>
  );
};

export default ContactMe;
