import React, { useContext } from 'react';
import styled from 'styled-components';
// context
import { StateContext } from '../Root';
// constants
import { MOBILE } from '../../constants/rs-breakpoints';
// components
import Navbar from './Navbar';

const Wrapper = styled.div`
  background-color: ${props => props.bgColor ? props.bgColor : '#000'};
  transition: all 0.3s ease-in;
  color: white;
  text-align: right;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 2fr 4fr; 
  header {
    margin: 4rem 1rem;
    @media screen and (max-width: ${MOBILE}px) {
      margin: 0.2rem 1rem;
      h1 {
        font-size: 1.6rem;
      }
      h3 {
        font-size: 0.8rem;
      }
    }
  }
  @media screen and (max-width: ${MOBILE}px) {
    grid-template-rows: auto;
  }
`;


const Sidebar = () => {
  const { state, dispatch } = useContext(StateContext);
  return (
    <Wrapper bgColor={state.currentColor} >
      <header>
        <h1>Arman Grigoryan</h1>
        <h3>Junior &#60;&#47;web&#62; developer</h3>
      </header>
      {window.innerWidth > 480 ? <Navbar /> : null}
    </Wrapper>
  )
}

export default Sidebar;
