import React, { useContext, useEffect, useRef } from 'react';
import styled from 'styled-components';
// context
import { StateContext } from '../Root';
// constants
import { PREV_COLOR, NEXT_COLOR } from '../../constants/state';
import { MOBILE } from '../../constants/rs-breakpoints';
// components
import WhoAmI from './WhoAmI';
import Education from './Education';
import Skills from './Skills';
import Portfolio from './Portfolio';
import ContactMe from './ContactMe';

const Wrapper = styled.div`
  background-color: black;
  color: #fff;
  height: 100%;
  overflow-y: scroll;
  scroll-behavior: smooth;
  padding: 6rem 5rem;
  @media screen and (max-width: ${MOBILE}px) {
    padding: 3rem 2rem;
  }
  h2, h3, h4 {
    color: ${props => props.color ? props.color : '#fff'};
  }
  h2 {
    font-size: 2rem;
  }
  h3 {
    font-size: 1.4rem;
  }
  p {
    font-size: 1.2rem;
    max-width: 800px;
  }
  ul {
    list-style-type: square;
    li {
      font-size: 1.2rem;
      margin: 1rem;
    }
  }
`;

const ContentWrapper = styled.div`
  display: grid;
  grid-template-columns: 1;
  grid-gap: 2rem;
  > * {
    padding-bottom: 1rem;
    border-bottom: 2px solid #fff;
    &:last-child {
      border-bottom: none;
    }
  }
`;




const Content = () => {
  // context
  const { state, dispatch } = useContext(StateContext);

  // ref
  const contentRef = useRef();

  // tracks user scroll and dynamically changes the main color of the app
  const trackScroll = () => {
    const parent = contentRef.current;

    let fixedScrollTop = 0;
    parent.addEventListener('scroll', () => {
      const scrollTop = parent.scrollTop;
      // scroll difference since last color change 
      const diff = scrollTop - fixedScrollTop;

      if(Math.abs(diff) > 500) {
        if(diff > 0) {
          console.log('dispatching next color')
          dispatch({ type: NEXT_COLOR });
        }
        else if(diff < 0) {
          console.log('dispatching prev color')
          dispatch({ type: PREV_COLOR });
        }
        fixedScrollTop = scrollTop;
      }
    });
  };

  useEffect(() => {
    trackScroll();
  }, []);

  return (
    <Wrapper color={state.currentColor} ref={contentRef} >
      <ContentWrapper>
        <WhoAmI />
        <Education />
        <Skills />
        <Portfolio />
        <ContactMe />
      </ContentWrapper>
    </Wrapper>
  );
};

export default Content;
