import { createGlobalStyle } from 'styled-components';

const globalStyle = createGlobalStyle`
  * {
    box-sizing: border-box;
    font-family: 'Ubuntu Condensed', sans-serif;
  }

  h1, h2, h3, h4, h5, h6 {
    font-weight: 400;
  }

  a {
    color: inherit;
    text-decoration: none;
  }
`;

export default globalStyle;