import React from 'react';
import styled from 'styled-components';
import { SiReact, SiFreecodecamp } from 'react-icons/si';
import { AiFillYoutube } from 'react-icons/ai';
import { HiArchive } from 'react-icons/hi';
import {
  Accordion,
  AccordionItem,
  AccordionItemHeading,
  AccordionItemButton,
  AccordionItemPanel,
} from 'react-accessible-accordion';
import './accordion-styles.css';
// components
import MernProjects from './ProjectsGroups/MernProjects';
import FreecodecampProjects from './ProjectsGroups/FreecodecampProjects';
import TutorialProjects from './ProjectsGroups/TutorialProjects';
import OlderProjects from './ProjectsGroups/OlderProjects';

const Wrapper = styled.div`
  .portfolio-grid {
    max-width: 800px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 48px;
  }
`;

const Portfolio = () => {
  return (
    <Wrapper id='portfolio'>
      <h2>Portfolio</h2>
      <Accordion 
        allowMultipleExpanded
        allowZeroExpanded 
        preExpanded={['mern']}
      >

        <AccordionItem uuid='mern'>
          <AccordionItemHeading>
            <AccordionItemButton>
                <SiReact /> MERN stack projects (most recent)
            </AccordionItemButton>
          </AccordionItemHeading>
          <AccordionItemPanel>
            <MernProjects />
          </AccordionItemPanel>
        </AccordionItem>

        <AccordionItem uuid='fcc'>
          <AccordionItemHeading>
            <AccordionItemButton>
              <SiFreecodecamp /> freecodecamp projects
            </AccordionItemButton>
          </AccordionItemHeading>
          <AccordionItemPanel>
            <FreecodecampProjects />
          </AccordionItemPanel>
        </AccordionItem>

        <AccordionItem uuid='tut'>
          <AccordionItemHeading>
            <AccordionItemButton>
              <AiFillYoutube /> projects created by following tutorials
            </AccordionItemButton>
          </AccordionItemHeading>
          <AccordionItemPanel>
            <TutorialProjects />
          </AccordionItemPanel>
        </AccordionItem>

        <AccordionItem uuid='old'>
          <AccordionItemHeading>
            <AccordionItemButton>
              <HiArchive /> older projects
            </AccordionItemButton>
          </AccordionItemHeading>
          <AccordionItemPanel>
            <OlderProjects />
          </AccordionItemPanel>
        </AccordionItem>

      </Accordion>
    </Wrapper>
  );
};

export default Portfolio;
