import React from 'react';
import styled from 'styled-components';

const Wrapper = styled.div`
  article {

  }
`;

const Education = () => {

  return (
    <Wrapper id='education'>
      <h2>Education</h2>
      <article>
        <p>
        I finished primary school in 2016 and started school at college which I finished in 2019 with a qualification of a computer operator. 
        Currently I’m a first-year computer science student. I speak English almost fluently, Russian at a speaking level and Armenian is my native language.
        </p>
        <h3>Formal education</h3>
        <ul>
          <li>Primary school (2007-2016)</li>
          <li>College (2016-2019)</li>
          <li>University (2020 - now)</li>
        </ul>
        <h3>Languages</h3>
        <ul>
          <li>English (advanced)</li>
          <li>Russian (intermediate)</li>
          <li>Armenian (native)</li>
        </ul>
      </article>
    </Wrapper>
  );
};

export default Education;
