import React, { useEffect, useState, useContext } from 'react';
import styled from 'styled-components';
import { useInView } from 'react-intersection-observer';
// context
import { StateContext } from '../../../../Root';


const StyledProgressBar = styled.div`
  width: 100%;
  h3 {
    color: #fff !important;
    margin: 0;
    margin-bottom: 1rem;
  }
  .progress {
    border: 2px solid ${props => props.color};
    background-color: #000;
    height: 2rem;
    div {
      background-color: ${props => props.color};
      width: ${props => props.percentage}%;
      height: 100%;
      transition: width 1s ease-in-out;
    }
  }
`;

const ProgressBar = ({ children, percentage }) => {

  // CONTEXT

  const {state, dispatch} = useContext(StateContext);
  
  // STATE

  const [ progressState, setProgressState] = useState({
    animate: false, 
    alreadyAnimated: false
  })

  // INTERSECTION OBSERVER

  const { ref, inView, entry } = useInView({
    initialInView: false,
  });

  // LIFECYCLE

  useEffect(() => {
    if(inView) {
      if(progressState.alreadyAnimated) return;
      else {
        setProgressState({
          animate: true, 
          alreadyAnimated: true
        })
      }
    }
  }, [inView]);

  return (
    <StyledProgressBar percentage={ progressState.animate ? percentage : 0 } color={state.currentColor} ref={ref} >
      <h3>{children}</h3>
      <div className='progress'>
        <div></div>
      </div>
    </StyledProgressBar>
  );
};

export default ProgressBar;
