import React, { useReducer } from 'react';
import styled from 'styled-components';
import GlobalStyles from './globalStyles';
// constants
import { WHO_AM_I_COLOR, EDUCATION_COLOR, SKILLS_COLOR, PORTFOLIO_COLOR, CONTACT_ME_COLOR } from '../constants/colors';
import { WHO_AM_I, EDUCATION, SKILLS, PORTFOLIO, CONTACT_ME } from '../constants/state';
import { SELECTION_CHANGE, NEXT_COLOR, PREV_COLOR } from '../constants/state';
import { MOBILE } from '../constants/rs-breakpoints';
// components
import Sidebar from './Sidebar';
import Content from './Content';

// state

const colorsOrdered = [
  WHO_AM_I_COLOR, 
  EDUCATION_COLOR, 
  SKILLS_COLOR,
  PORTFOLIO_COLOR, 
  CONTACT_ME_COLOR
];

const elementsOrdered = [
  WHO_AM_I, 
  EDUCATION, 
  SKILLS,
  PORTFOLIO, 
  CONTACT_ME
];

const initialState = {
  elementChosen: null,
  colorNum: 0,
  currentColor: colorsOrdered[0]
};

const reducer = (state = initialState, action) => {
  if (action.type === SELECTION_CHANGE) {
    const elemNum = elementsOrdered.indexOf(action.payload);
    if(elemNum !== -1) return {
      ...state,
      elementChosen: elementsOrdered[elemNum]
    }
    return state;
  }
  else if (action.type === NEXT_COLOR) { 
    let newColorNum = state.colorNum + 1;
    if(newColorNum > 4) newColorNum = 0;
    return {
      ...state,
      colorNum: newColorNum,
      currentColor: colorsOrdered[newColorNum]
    }
  } 
  else if (action.type === PREV_COLOR) { 
    let newColorNum = state.colorNum - 1;
    if(newColorNum < 0) newColorNum = 4;
    return {
      ...state,
      colorNum: newColorNum,
      currentColor: colorsOrdered[newColorNum]
    }
  }
  else return state;
};

// context

export const StateContext = React.createContext();

// style

const RootWrapper = styled.div`
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr 4fr;
  @media screen and (max-width: ${MOBILE}px) {
    grid-template-columns: 1fr;
  }
`; 


// component

const Root = () => {
  const [state, dispatch] = useReducer(reducer, initialState);

  return (
    <StateContext.Provider value={{ state, dispatch }}>
      <GlobalStyles />
      <RootWrapper>
        <Sidebar />
        <Content />
      </RootWrapper>
    </StateContext.Provider>
  );
};

export default Root;
