import React from 'react';
// assets
import huzerangnerDemo from '../../../../assets/Portfolio/mern/huzerangner_demo.jpg';
import breezeWeatherDemo from '../../../../assets/Portfolio/mern/breezeweather_demo.jpg';
import notesDeskDemo from '../../../../assets/Portfolio/mern/notesdesk_demo.jpg';
// components
import ProjectsGrid from './ProjectsGrid';
import PortfolioItem from './PortfolioItem';

const MernProjects = () => {
  return (
    <ProjectsGrid>
        <PortfolioItem 
          title='Huzerangner'
          description='personal blog'
          stack='react/redux, express.js, mongoDB'
          image={huzerangnerDemo}
          githubLink='https://github.com/armanDark/huzerangner-backend'
          demoLink='https://huzerangner.netlify.app/?page=1'
        />
        <PortfolioItem 
          title='BreezeWeather'
          description='weather forecast application'
          stack='react.js, openweather api'
          image={breezeWeatherDemo}
          githubLink='https://github.com/armanDark/breeze-weather-react'
          demoLink='https://armandark.github.io/breeze-weather-react/'
        />
        <PortfolioItem 
          title='NotesDesk'
          description='simple sticky notes app'
          stack='react/redux'
          image={notesDeskDemo}
          githubLink='https://github.com/armanDark/notes-desk-react'
          demoLink='https://armandark.github.io/notes-desk-react/'
        />
    </ProjectsGrid>
  );
};

export default MernProjects;
