import React, { useContext } from 'react';
import styled from 'styled-components';
// context
import { StateContext } from '../../Root';
// assets
import myPicture from '../../../assets/WhoAmI/me.jpg';

const Wrapper = styled.div`
  article {
    img {
      border: 6px solid ${props => props.color};
      max-width: 300px;
      width: 100%;
    }
  }
`;

const WhoAmI = () => {
  const {state, dispatch} = useContext(StateContext);

  return (
    <Wrapper id='whoami' color={state.currentColor} >
      <h2>Who Am I?</h2>
      <article>
        <img src={myPicture} alt='my portrait' ></img>
        <p>
        Hey there :) I’m Arman Grigoryan, a 19 years old first-year computer science student who’s very passionate about web development. <br/>
        My main and favourite programming language is JavaScript, but I’m always open to learning new technologies. <br/>
        Fun fact about me: 
        the first project I made back in 2018 was a simple static website made with only html and css and showed information about Brazil, 
        and the last one is a full-stack personal blog website for my friend. What do those two projects share in common? The love they’re made with ❤ 
        </p>
      </article>
    </Wrapper>
  );
};

export default WhoAmI;
