import React from 'react';
// assets
import memoriesDemo from '../../../../assets/Portfolio/tut/memories_demo.jpg';
import DevConnectorThemeDemo from '../../../../assets/Portfolio/tut/devconnectortheme_demo.jpg';
import tacosDemo from '../../../../assets/Portfolio/tut/tacos_demo.jpg';
import blogLayoutDemo from '../../../../assets/Portfolio/tut/bloglayout_demo.jpg';
// components
import ProjectsGrid from './ProjectsGrid';
import PortfolioItem from './PortfolioItem';

const FreecodecampProjects = () => {
  return (
    <ProjectsGrid>
      <PortfolioItem 
          title='Memories'
          description='Social media app'
          stack='react/redux, express.js, mongoDB'
          image={memoriesDemo}
          githubLink='https://github.com/armanDark/memories_fullstack'
          demoLink={null}
        />
        <PortfolioItem 
          title='DevConnector theme'
          description='Dev network theme'
          stack='html/css, sass'
          image={DevConnectorThemeDemo}
          githubLink='https://github.com/armanDark/devconnector-theme'
          demoLink='https://armandark.github.io/devconnector-theme/'
        />
        <PortfolioItem 
          title='Tacos'
          description='Responsive restaurant layout'
          stack='html/css, grid & flexbox'
          image={tacosDemo}
          githubLink='https://github.com/armanDark/grid-tacos'
          demoLink='https://armandark.github.io/grid-tacos/'
        />
        <PortfolioItem 
          title='Blog layout'
          description='Blog layout using grid'
          stack='html/css, grid'
          image={blogLayoutDemo}
          githubLink='https://github.com/armanDark/full-bleed-blog-layout'
          demoLink='https://armandark.github.io/full-bleed-blog-layout/'
        />
    </ProjectsGrid>
  );
};

export default FreecodecampProjects;
