import React, { useContext } from 'react';
import styled from 'styled-components';
// constants
import { WHO_AM_I, EDUCATION, SKILLS, PORTFOLIO, CONTACT_ME } from '../../../constants/state';
import { SELECTION_CHANGE } from '../../../constants/state';
// context
import { StateContext } from '../../Root';

const Wrapper = styled.div`
  nav {
    a {
      display: block;
      font-size: 2rem;
      margin: 2rem 1rem;
    }
    a[data-element='${props => props.elementChosen ? props.elementChosen : null}'] {
      color: #000;
    }
  }
`;


const Navbar = () => {
  const { state, dispatch } = useContext(StateContext);

  const clickHandler = (e) => {
    const selectedElement = e.target.getAttribute('data-element');
    dispatch({ type: SELECTION_CHANGE, payload: selectedElement });
  };

  return (
    <Wrapper elementChosen={state.elementChosen ? state.elementChosen : null} >
      <nav>
        <a href='#whoami' data-element={WHO_AM_I} onClick={clickHandler} >Who am I?</a>
        <a href='#education' data-element={EDUCATION} onClick={clickHandler} >Education</a>
        <a href='#skills' data-element={SKILLS} onClick={clickHandler} >Skills</a>
        <a href='#portfolio' data-element={PORTFOLIO} onClick={clickHandler} >Portfolio</a>
        <a href='#contactme' data-element={CONTACT_ME} onClick={clickHandler} >Contact me</a>
      </nav>
    </Wrapper>
  )
}

export default Navbar;
