import React, { useContext } from 'react';
import styled from 'styled-components';
import { AiFillGithub } from 'react-icons/ai';
// context
import { StateContext } from '../../../../Root';

const Wrapper = styled.div`
  header {
    margin: 1rem 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .header-name {
      * {
        margin: 0;
      }
      h4 {
        font-size: 1.4rem;
        margin-bottom: 0.5rem;
      }
      h5 {
        font-size: 1rem;
        font-weight: 400;
        opacity: 0.8;
      }
    }
    .header-links {
      display: flex;
      justify-content: center;
      > * {
        margin: 0.5rem;
      }
    }
    
  }
  .picture {
    border: 4px solid ${props => props.color};
    max-width: 100%;
    img {
      width: 100%;
      aspect-ratio: 16 / 10;
      object-fit: cover;
    }
  }
  footer {
    p {
      font-size: 1rem;
  }
`;

const PortfolioItem = ({title, description, stack, image, githubLink, demoLink}) => {
  const { state, dispatch } = useContext(StateContext);

  return (
    <Wrapper color={state.currentColor} className='animate__animated animate__jackInTheBox'>
      <header>
        <div className='header-name'>
          <h4>{title}</h4>
          {description ? <h5>{description}</h5> : null}
        </div>
        <div className='header-links'>
          <a href={githubLink} target='_blank' rel="noreferrer">
            <AiFillGithub />
          </a>
          {demoLink ?  <a href={demoLink} target='_blank' rel="noreferrer">Demo</a> : null}
        </div>
      </header>
      <div className='picture'>
        <img src={image} alt={description} />
      </div>
      <footer>
        <p>{stack}</p>
      </footer>
    </Wrapper>
  );
};

export default PortfolioItem;
