import React from 'react';
// assets
import gridAlbumLayoutDemo from '../../../../assets/Portfolio/older/gridalbumlayout_demo.jpg';
import digitalClockDemo from '../../../../assets/Portfolio/older/digitalclock_demo.jpg';
// components
import ProjectsGrid from './ProjectsGrid';
import PortfolioItem from './PortfolioItem';


const OlderProjects = () => {
  return (
    <ProjectsGrid>
      <PortfolioItem 
        title='Grid Album Layout'
        description='Album layout using JS and css grid'
        stack='vanilla.js, css grid'
        image={gridAlbumLayoutDemo}
        githubLink='https://github.com/armanDark/grid-album-layout'
        demoLink='https://armandark.github.io/grid-album-layout/'
      />
      <PortfolioItem 
        title='Digital Clock'
        description='A deeply customizable digital clock'
        stack='jQuery'
        image={digitalClockDemo}
        githubLink='https://github.com/armanDark/clock-app'
        demoLink='https://armandark.github.io/clock-app/'
      />
    </ProjectsGrid>
  )
}

export default OlderProjects;
